<template>
    <section class="post-form">
        <h2>–°–æ–∑–¥–∞—Ç—å –Ω–æ–≤—ã–π –ø–æ—Å—Ç:</h2>
        <section class="post-form__container" title="–ù–∞—Å—Ç—Ä–æ–π–∫–∏ –ø—Ä–∏–º–µ–Ω—è—é—Ç—Å—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏.">
            <label class="post-form__text">
                –ù–∞–∑–≤–∞–Ω–∏–µ:
            </label>
            <my-input
                class="post-form__input"
                :type="'text'"
                v-model="post.title"
            />
            <label class="post-form__text">
                –û–ø–∏—Å–∞–Ω–∏–µ:
            </label>
            <my-input
                class="post-form__input"
                :type="'text'"
                v-model="post.body"
            />
            <div class="post-form__actions">
                <my-button v-if="changeMode" @click="$emit('closeDialog')">–û—Ç–º–µ–Ω–∞</my-button>
                <my-button v-if="changeMode" @click="$emit('changePost', post)">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å </my-button>
                <my-button v-else @click="$emit('createPost', this.post)">–°–æ–∑–¥–∞—Ç—å –ø–æ—Å—Ç</my-button>
            </div>
        </section>
    </section>
</template>

<script>

export default {
    name: "post-form",
    props: {
        changeMode:{
            type: Boolean,
            default: false
        },
        postToChange:{
            type: Object
        }

    },
    data(){
        return {
            post:{
                userId: 1,
                id: 1,
                title: '',
                body: ''
            }
        }
    },
    methods:{
        loadPost(){
            if (this.postToChange !== undefined){
                this.post = {...this.postToChange};
            }
        },
    },
    mounted() {
        this.loadPost();
    }
}
</script>

